                };
                this.selectAnationalityList.push(obj);
              }
            });
          }

          if (jobMessage.Prefered_Work_Schedule_Type) {
            let arr = jobMessage.Prefered_Work_Schedule_Type;
            arr.forEach(item => {
              if (item.object_id == 0) {
                this.selectWorkTypeList.push(item.object_en);
              } else {
                let obj = {
                  id: item.object_id,
                  pid: item.object_pid,
